






































































form {
  width: 75%;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  
}

.search-input {
  -webkit-box-flex: 2;
      -ms-flex-positive: 2;
          flex-grow: 2;
  padding: 9px;
  font-size: 1.5em;
  border: 1px solid #bbbbbb;
  box-shadow: none;
  box-sizing: border-box;
}

.search-input:focus {
  border: 1px solid #2b2b2b;
  outline: 0;
}

.submit-button {
  position: relative;
  left: -1px;
  padding: 9px;
  font-size: 1.5em;
  background-color: #2b2b2b;
  color: #d1d1d1;
  border: 1px solid #727272;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
  width: 10%;
  min-width: 55px;
  -webkit-transition: color .1s ease-out;
  transition: color .1s ease-out;
  opacity: 0.8;
}


.search-input:focus + .submit-button {
  opacity: 1;
  color: #e81c4f;
}

.grid {
  margin-top: 1%;
}




